 export default{
  data () {
   return {
   	title:{
            name:"propagandainfo",
            flag:false,
            backUrl:""
       },
       widthnum:0,
       infoObj:{},
       requestUrl:"",
			 isAndroid:false,
			 queryid:'',
   }
  },
  mounted () {
		window.setI18nValue = this.setI18nValue;//国际化赋值
		window.goBack = this.goBack;//返回
  },
  created(){
		this.isAndroid=this.$isAndroid();
  	this.requestUrl=this.$myConfig.ip+":"+this.$myConfig.port;
  	var winW = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
  	this.widthnum=winW;
  	this.queryid= this.$route.query.queryid;
  	console.log("infoid===>"+this.queryid)
		if(this.isAndroid){
			this.requestData(this.queryid)
			window.android.enableJsBackPressed();
		}else{
			this.requestData(this.queryid)
		}
  	
		
  },
  methods: {
	setI18nValue(i18nstr,infoId){
		this.$i18n.locale = i18nstr;
		this.requestData(infoId);
	},
	backList(){
		var backUrl="/propaganda"
		this.$router.push(backUrl);
		
	},
  requestData(queryId){
  	var _this=this;
		var i18nstr=_this.$i18n.locale;
		if(i18nstr!=''){
			i18nstr=i18nstr.split("-")[0];
		}
  	var  qurl=_this.requestUrl+"sciencepopularization/detail"
		var param={"queryid":queryId,"language":i18nstr}
		_this.$vux.loading.show({text: 'Loading'})

		console.log(param)
		 _this.$commonAjax("GET",qurl,param,function(data){
			 _this.$vux.loading.hide()
			 console.log(data);
			 if(data.data!=""){
				 var info=JSON.parse(data.data)
				 console.log(info);
				 _this.infoObj=info[0];
				 var publishtimeStr=_this.infoObj.publishtime;
				 if(publishtimeStr.indexOf(".")>0){
					 console.log("ddd==>"+publishtimeStr)
					 _this.infoObj.publishtime=publishtimeStr.split(".")[0];
				 }
				 
			 }
				_this.handlePage()
		 },function(){
			 _this.$vux.loading.hide()
			 _this.showBottomToast(_this.$t('requesterror'))
			 var appData = require("../../assets/json/propagandainfochange.json");
				 console.log(appData)
				 _this.infoObj=appData.data;
		},true)
  },
  showBottomToast(val){
	  // 显示
	  this.$showBottomToast(val, 'bottom')
	},
	handlePage(){
		let _this=this;
		console.log("处理页面。。。")
		setTimeout(()=>{
			$("video").css({ width: "100%"});
			console.log($(".info_body img"));
			$(".info_body img").css({ width: "100%"});
			$(".info_body img").on("click",function(){
				var imgurl=this.src;
				if(imgurl!=undefined&&imgurl!=''){
					console.log(this.src);
					if(_this.isAndroid){
						console.log("is android!")
						window.android.openImage(imgurl);
					}else{
						console.log("is ios")
						window.location.href="openImage:"+imgurl;
					}
				}
				
			})
		},50)
		
	},goBack() {
      var backUrl=this.title.backUrl;
      if(backUrl!=undefined&&backUrl!=null&&backUrl!='null'&&backUrl!=''){
      	  this.$router.push(backUrl);
      }else{
      	 this.$router.go(-1); //返回上一次历史记录
      }

    },
  }
 }



